    height: 100%;
    line-height: normal;
    background: #131418;
    margin: 0;
    padding: 0;
    margin-top: 3rem;
    border-radius: 3px;
    .commit_box {
        width: 55%;
        height: 10rem;
        margin: 0 auto;
        background-color: white;
        position: relative;
        border-radius: 4px;
        box-shadow: 0 0 12px -1px #efefef;
        textarea {
            width: 90%;
            border: none;
            padding: 1rem;
            margin: 0px;
            resize: none;
            overflow: hidden;
            padding: 20px 0;
            resize: none;
            border-radius: 0;
            outline: none;
        }
        .btn {
            position: absolute;
            top: 73%;
            right: 2%;
            display: flex;
            height: 28px;
            margin: 0 auto;
            padding: 0 20px;
            justify-content: center;
            -webkit-transition-duration: 0.4s;
            transition-duration: 0.4s;
            text-align: center;
            color: #444444;
            border: 1px solid #f7f7f7;
            border-radius: 30px;
            background-color: #f7f7f7;
            align-items: center;
            cursor: pointer;
            font-size: 15px;
            &:hover {
                border: 1px solid #eb5055;
                color: #eb5055;
            }
        }
    }
    .comment-view {
        width: 52%;
        margin: 2rem auto;
        background-color: #303131;
        position: relative;
        padding: 20px;
        transition: all 0.5s;
        cursor: pointer;
        &:hover {
            // box-shadow: 0 0 10px -1px #eaeaea;
            // background:#f3f3f3;
            transition: all 0.5s;
        }
        .comment-header {
            display: inline-block;
            width: 100%;
            img {
                display: inline-block;
                float: left;
                width: 40px;
                height: 40px;
                // border: 1px solid #eaeaea;
                border-radius: 50%;
            }
            span {
                font-size: 13px;
                line-height: 45px;
                display: inline-block;
                float: left;
                margin: 0 10px;
                display: flex;
                justify-content: center;
                align-items: center;
            }
        }
        .comment-content {
            margin-bottom: 10px;
            color: #313131;
            text-align: left;
        }
        .comment-meta {
            font-size: 12px;
            text-align: left;
            transition: all 0.3s;
            i {
                &:hover {
                    color: white;
                    transition: all 0.3s;
                }
            }
        }
    }
}
.markdown-body {
    box-sizing: border-box;
    min-width: 200px;
    max-width: 980px;
    margin: 0 auto;
    padding: 0 45px;
}

.fave_box {
    margin: 2rem 15px;
    width: 105px;
    height: 40px;
    border: 1px solid #303131;
    -webkit-transition: all 0.6s;
    transition: all 0.6s;
    display: flex;
    border-radius: 37px;
    align-items: center;
    color: #f78589;
    cursor: pointer;
}
.fave {
    width: 50px;
    height: 50px;
    background: url("http://dlhtx.zicp.vip:3000/img/dianzan.png") no-repeat;
    background-position: left;
    background-size: auto 100%;
}

.faveActive {
    background-color: #ffffff !important;
    transition: all 0.6s;
    color: white;
}

.active {
    background-position: right;
    /* 主要在这一步 */
    transition: background 0.6s steps(19);
}

@media (max-width: 767px) {
    .markdown-body {
        padding: 15px;
    }
}
</style>




